import React, { useState, useEffect } from "react";
import axios from "axios";
import Button from "@material-ui/core/Button";

import Box from "@material-ui/core/Box";
import "./App.css";

function App() {
	const [quote, setQuote] = useState("");
	const [firstName, setFirstName] = useState("");
	const [lastName, setLastName] = useState("");
	const [click, setClick] = useState(0);

	useEffect(() => {
		const fetchOffice = async () => {
			const res = await axios(`/api/quotes/random`);
			setQuote(res.data.data.content);
			setFirstName(res.data.data.character.firstname);
			setLastName(res.data.data.character.lastname);
		};
		fetchOffice();
	}, [click]);

	return (
		<div>
			<Box className="box" component="span" m={1}>
				<h1 className="logo">The Office</h1>
				<p className="quote">{`"${quote}"`}</p>
				<p className="worker">{`${firstName} ${lastName} `}</p>
				<Button onClick={() => setClick(click + 1)} variant="contained">
					<p className="btn">Dalje</p>
				</Button>
			</Box>
		</div>
	);
}

export default App;
